@tailwind base;
@tailwind components;
@tailwind utilities;

/* Base Styles */

/* Styl dla całego paska przewijania */
::-webkit-scrollbar {
    width: 10px; /* szerokość paska przewijania */
    height: 10px; /* wysokość paska przewijania (dla poziomego) */
  }
  
  /* Styl dla tła paska przewijania */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; /* kolor tła paska przewijania */
    border-radius: 10px; /* zaokrąglone rogi */
  }
  
  /* Styl dla suwaka paska przewijania */
  ::-webkit-scrollbar-thumb {
    background: #888; /* kolor suwaka */
    border-radius: 10px; /* zaokrąglone rogi suwaka */
  }
  
  /* Styl dla suwaka podczas najechania myszką */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; /* ciemniejszy kolor suwaka przy hover */
  }
  
  /* Styl dla paska przewijania poziomego */
  ::-webkit-scrollbar-horizontal {
    height: 8px; /* wysokość paska przewijania poziomego */
  }
  

/* Body */
body {
    font-family: 'Arial', sans-serif;
    background-color: #f9fafb; /* Light background color */
    color: #333; /* Default text color */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Typography */
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Helvetica Neue', sans-serif;
    font-weight: bold;
  }
  
  p {
    line-height: 1.6;
    margin-bottom: 1.5rem;
  }
  
  /* Link styles */
  a {
    color: #007bff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  a:hover {
    color: #0056b3;
  }
  
  /* Button Styles */
  button {
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  button:hover {
    transform: scale(1.05);
  }
  
  /* Utility Classes */
  .text-center {
    text-align: center;
  }
  
  .text-lg {
    font-size: 1.125rem;
  }
  
  .text-xl {
    font-size: 1.25rem;
  }
  
  .font-bold {
    font-weight: 700;
  }
  
  .font-semibold {
    font-weight: 600;
  }
  
  .font-extrabold {
    font-weight: 800;
  }
  
  .bg-gray-50 {
    background-color: #f9fafb;
  }
  
  .bg-white {
    background-color: white;
  }
  
  .text-white {
    color: white;
  }
  
  .text-gray-800 {
    color: #2d3748;
  }
  
  .text-gray-600 {
    color: #718096;
  }
  
  .text-gray-900 {
    color: #1a202c;
  }
  
  /* Card Styles */
  .card {
    background-color: white;
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .card:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
  }
  
  /* Layout helpers */
  .container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  /* Flexbox Helpers */
  .flex {
    display: flex;
  }
  
  .items-center {
    align-items: center;
  }
  
  .justify-center {
    justify-content: center;
  }
  
  .flex-col {
    flex-direction: column;
  }
  
  .flex-row {
    flex-direction: row;
  }
  
  /* Grid Layout */
  .grid {
    display: grid;
  }
  
  .grid-cols-1 {
    grid-template-columns: repeat(1, 1fr);
  }
  
  .grid-cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .grid-cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .gap-8 {
    gap: 2rem;
  }
  
  /* Section padding */
  .py-16 {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
  
  .py-20 {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
  
  /* Hero Section */
  .hero-section {
    background-image: url('https://source.unsplash.com/random/1600x900');
    background-size: cover;
    background-position: center;
    color: white;
    text-align: center;
    padding: 10rem 0;
  }
  
  .hero-section h1 {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }
  
  .hero-section p {
    font-size: 1.25rem;
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .text-xl {
      font-size: 1.125rem;
    }
  
    .text-lg {
      font-size: 1rem;
    }
  
    .hero-section {
      padding: 5rem 0;
    }
  
    .card {
      padding: 1.5rem;
    }
  
    .grid-cols-1 {
      grid-template-columns: repeat(1, 1fr);
    }
  
    .grid-cols-2 {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
  /* Transition Utilities */
  .transition-all {
    transition: all 0.3s ease-in-out;
  }
  
  .transform {
    transform: scale(1);
  }
  
  .hover\:scale-105:hover {
    transform: scale(1.05);
  }
  
  .hover\:scale-110:hover {
    transform: scale(1.1);
  }
  